import { Todo, Action, ActionTypes } from "../actions";

const defaultTodos = [
  {
    id: 1,
    content: "",
    completed: false,
  },
  {
    id: 2,
    content: "",
    completed: true,
  },
  {
    id: 3,
    content: "",
    completed: false,
  },
  {
    id: 4,
    content: "",
    completed: true,
  },
  {
    id: 5,
    content: " vicky ",
    completed: false,
  },
  {
    id: 6,
    content: " ada ",
    completed: false,
  },
];

export const todoReducer = (state: Todo[] = defaultTodos, action: Action) => {
  switch (action.type) {
    case ActionTypes.addTodos:
      const newTodo = {
        id: Date.now(),
        content: action.payload,
        completed: false,
      };
      return [...state, newTodo];
    case ActionTypes.checkTodos:
      return state.map((i: Todo) => {
        return i.id === action.payload
          ? { ...i, completed: !i.completed }
          : { ...i };
      });
    case ActionTypes.deleteTodos:
      return state.filter((i: Todo) => {
        return i.id !== action.payload;
      });
    case ActionTypes.deleteAllTodos:
      return state.filter((i: Todo) => {
        return i.completed !== true;
      });
    default:
      return state;
  }
};
